import React from 'react'
import propTypes from 'prop-types';
import './Prueba.css';


const  Prueba= ({url,lang,fcolor,scolor}) => {
    
    return (
        <div className="card">
            <img src={url} alt="logo.png"/> 
            <h1> {lang} </h1>


        </div>
    )
}
Prueba.propTypes={
    lang:propTypes.string,
    url:propTypes.string
}


export default Prueba

